.navi {
    color: #F5F5F5;
    font-weight: bold;
    margin: 1rem 2.5rem;
    text-decoration: none;
}

.navi:hover, 
.drop_main:hover,
.drop:hover {
    color: #88FF09;
}

.navi:active {
    color: white;
}

.drop {
    font-weight: bold;
    margin: .5rem 2.5rem;
    text-decoration: none;
}

.drop_main {
    color: white;
    font-weight: bold;
}
