.desc {
    color: #88FF09;
    font-weight: bolder;
    font-size: 500%;
}

.mt_100 {
    margin-top: 20px
}
.descBtn, .descBtn:hover {
    border: .2rem solid #CEFF1A;
    background: transparent;
    padding: .7rem 2.5rem;
    color: #F5F5F5;
    text-decoration: none;
}

.descBtn2, .descBtn2:hover {
    border: .2rem solid #CEFF1A;
    text-decoration: none;
    background: transparent;
    padding: .5rem 1rem;
    color: #F5F5F5;
}

.vertical {
    color: #88FF09;
    font-size: 500%;
}

.hoz {
    color: #88FF09;
    font-size: 500%;
}

.btn{
    background-color: #88FF09;
    color: white;
    padding: .5rem .9rem;
    border: none;
}

.icon, .icon:hover{
    color: #88FF09;
    padding: .5rem .9rem;
    border: none;
}

.service_icon {
    color: #CEFF1A;
    font-size: 250%;
}

.service_col {
    height: 20rem;
    padding: 4rem;
    background-color: #191919;
    border-radius: 5px;
}

.service_col.first {
    border: 2px solid transparent;
    box-shadow: -5px -9px #88FF09;
}
.service_col.last {
    border: 2px solid transparent;
    box-shadow: 5px 9px #88FF09;
}

.service_col:hover{
    background-color: #88FF09;
    color: white;
}

.col_4 {
    transition-property: margin;
    transition-duration: .5s;
    transition-timing-function: linear;
}

.col_4:hover{
    margin-top: -1.5rem; 
}
.pbar {
    display: none;
}

@media screen and (max-width: 360px) {
    .desc {
        font-size: 150%;
    }
    .descDiv{
        margin: 0;
        padding: .5rem !important;
        color: yellow;
    }
    .desc_text{
        width: 100%;
    }
    .line_dash hr {
        text-align: center;
        display: none;
    }
    .outer_img_div{
        position: fixed;
        display: block;
        margin-top: 10rem;
        margin-bottom: 8rem;
    }
    .img1{
        height: 18rem;
        width: 20rem;
    }
    .vertical {
        font-size: 300%;
        text-align: center;
        margin-left: 50%;
        margin-right: 50%;
    }
    .mt_100 div{
        display: none;
    }
    .br {
        display: none;
    }
    .pbar {
        margin-top: -5rem;
        display: block;
    }
    .service_col {
        height: 20rem;
        padding: 2rem;
        background-color: #191919;
        border-radius: 5px;
        margin-bottom: 2.5rem;
    }
}

@media screen and (min-width: 360px) {
    .desc {
        font-size: 150%;
    }
    .descDiv{
        margin: 0;
        padding: .5rem !important;
    }
    .desc_text{
        width: 100%;
    }
    .line_dash hr {
        text-align: center;
        display: none;
    }
    .outer_img_div{
        position: fixed;
        display: block;
        margin-top: 10rem;
        margin-bottom: 8rem;
    }
    .img1{
        height: 18rem;
        width: 20rem;
    }
    .vertical {
        font-size: 300%;
        text-align: center;
        margin-left: 50%;
        margin-right: 50%;
    }
    .mt_100 div{
        display: none;
    }
    .br {
        display: none;
    }
    .pbar {
        margin-top: -5rem;
        display: block;
    }
    .service_col {
        padding: 2rem;
        margin-bottom: 2.5rem;
    }
}

