    .navBrand {
        background-color: blue;
    }
} */

@media screen and (min-width: 768px) {
    .vertical {
        margin-left: 0;
        margin-right: 0;
    }
    .service_col {
        height: 21rem;
        padding: 1rem;
        margin-bottom: 2.5rem;
    }
    .line_dash hr {
        text-align: center;
        display: block;
        width: 95% !important;
    }
}

@media screen and (min-width: 992px) {
    .desc {
        font-size: 300%;
    }
    .descDiv {
        padding: 2rem !important;
    }
    .main_skill {
        font-size: 250% ;
        padding-bottom: .8rem;
    }
    .skill_text {
        text-align: center;
    }
    .service_col {
        padding: 2.5rem;
    }
    .line_dash hr {
        width: 97% !important;
    }
    .mt_100 div{
        display: inline-block;
    }
    .br {
        display: block;
    }
    .pbar {
        margin-top: -5rem;
        display: none;
    }
}

@media screen and (min-width: 1200px) {
    .desc {
        font-size: 400%;
    }
    .descDiv {
        padding: 3rem !important;
    }
    .software{
        padding: 0;
    }
    .desc_text{
        width: 22rem;
    }
    .main_skill {
        font-size: 500% ;
        padding-bottom: .8rem;
    }
}

@media screen and (min-width: 1440px) {
    .desc {
        font-size: 500% !important;
    }
    .descDiv {
        padding: 4rem !important;
    }
}