.progress_title{
    font-size: 16px;
    font-weight: 700;
    color: white;
    margin: 0 0 20px;
}
.progress{
    height: 10px;
    background: #88FF09;
    border-radius: 0;
    box-shadow: none;
    margin-bottom: 30px;
    overflow: visible;
}
.progress .progress_bar{
    position: relative;
    -webkit-animation: animate-positive 2s;
    animation: animate-positive 2s;
}
.progress .progress_bar:after{
    content: "";
    display: inline-block;
    width: 9px;
    background: #fff;
    position: absolute;
    top: -10px;
    bottom: -10px;
    right: -1px;
    z-index: 1;
    transform: rotate(35deg);
}
.progress .progress_value{
    display: block;
    font-size: 16px;
    font-weight: 600;
    color: white;
    position: absolute;
    top: -30px;
    right: -25px;
}